<template>
  <div>
    <v-sheet color="rgba(0,0,0,0.8)">
      <v-container class="" color="red">
        <v-row>
          <v-col cols="12">
            <p class="text-h4 text-center white--text">RERCUPERAR CONTRASEÑA</p>
          </v-col>
        </v-row>
        <v-row justify="center" class="white--text">
          <v-col cols="12" md="3">
            <p>Ingrese Su Número De Trabajador</p>
            <v-otp-input
              dark
              length="7"
              plain
              type="password"
              :disabled="loading"
              v-model="num"
              @finish="Actualizar()"
            ></v-otp-input>
            <v-overlay absolute :value="loading">
              <v-progress-circular
                indeterminate
                color="primary"
              ></v-progress-circular>
            </v-overlay>
          </v-col>
        </v-row>
      </v-container>
    </v-sheet>
  </div>
</template>
<script>
import axios from "axios";
import { mapMutations, mapState } from "vuex";
export default {
  components: {},
  computed: {
    ...mapState(["ip"]),
  },
  name: "Recuperar_Con",
  data() {
    return {
      valido: true,
      num: "",
      loading: false,
    };
  },
  methods: {
    async Actualizar() {
      this.loading = true;
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Cargando Faltas");
      this.Modificar_Espera(true);
      try {
        
        const json = JSON.stringify({id:this.num});      
//      console.log(json);
        // let res = await axios.post(
        //   "http://" + this.ip + "/api/jefe_departamento/Falta_Justificada",
        //   json,
        //   {
        //     headers: {
               // Overwrite Axios's automatically set Content-Type
        //       "Content-Type": "application/json",
        //     },
        //   }
        // );
        let res = await axios.post(
          "http://" + this.ip + "/api/docente/recuperacion",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        
        if (!this.error && !res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Se Mando Un Correo Con Su Nueva Contraseña");          
          this.Modificar_Boton(true);    
          this.loading = false; 
          this.$router.push("/login")
        } else if (res.data.Error) {
          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          if(res.data.status==3){
              this.Modificar_Datos("No se encontro ningun empleado con ese numero");
          }else if (res.data.status==2){
              this.Modificar_Datos("El correo no es correcto decir a Capital Humano");
          }else{
              this.Modificar_Datos(res.data.status);
          }
          
          this.Modificar_Boton(true);
          this.loading = false;
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        this.loading = false;
        console.log(error);
      }
    },
    ...mapMutations([
      "Modificar_cargar",
      "Checar_Token",
      "Salir",
      "Modificar_Informacion",
      "Modificar_Espera",
      "Modificar_Correcto",
      "Modificar_Datos",
      "Modificar_Boton",
      "Modificar_ruta",
    ]),
  },
};
</script>